/* ==========================================================================
   Components Galleries
   ========================================================================== */

.galleries__item {
  $gt: &;
  #{$gt}__box {
    overflow: hidden;
  }
  &__img {
    padding-bottom: 80%;
    position: relative;
    transition: all .3s ease-in-out;
    &:hover {
      transform: scale(1.1);
    }
  }
  &__img__el {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}

